// /pages/posts/[slug].tsx
import Link from "next/link";
import { GetStaticPaths, GetStaticProps } from "next";
import ReactMarkdown from "react-markdown";
import Header from "../../components/Header"; // Áî® Header ÁªÑ‰ª∂
import { getAllPosts, getPostBySlug, Post } from "../../lib/posts";
import { tagMap } from "../../lib/tags";
import { formatDate } from "../../lib/date";

interface PostPageProps {
  post: Post;
}

export default function PostPage({ post }: PostPageProps) {
  return (
    <div className="container">
      {/* ‰ΩøÁî® Header ÁªÑ‰ª∂ÔºåÂè™ÊòæÁ§∫‰∏Ä‰∏™ Posts ÊåâÈíÆ */}
      <Header buttons={[{ label: "Posts", href: "/posts" }]} />
      {/* ÊñáÁ´†ÂÜÖÂÆπ */}
      <main>
        {post.cover && (
          <img
            src={post.cover}
            alt={post.title}
            style={{
              width: "100%",
              height: "250px",
              objectFit: "cover",
              borderRadius: "1px",
              marginBottom: "1rem",
            }}
          />
        )}
        <h1>{post.title}</h1>
        <p style={{ color: "#888", marginBottom: "1rem" }}>
          {formatDate(post.date)}
        </p>

        <ReactMarkdown>{post.content}</ReactMarkdown>

        {/* Tags Âå∫Âüü */}
        {post.tags && post.tags.length > 0 && (
          <div style={{ marginTop: "2rem" }}>
            <div style={{ display: "flex", gap: "0.5rem", flexWrap: "wrap" }}>
              {post.tags.map((tag) => (
                <Link key={tag} href={`/tags/${tag}`}>
                  <span
                    style={{
                      padding: "0.25rem 0.5rem",
                      borderRadius: "5px",
                      // backgroundColor: "#f0f0f0",
                      cursor: "pointer",
                      fontSize: "0.85rem",
                      color: "#706F6C",
                      display: "flex",
                      alignItems: "center",
                      gap: "0.2rem", // ÂõæÊ†áÂíåÊñáÂ≠óÈó¥Ë∑ù
                    }}
                  >
                    <img
                      src="/icon/pound.svg"
                      alt="#"
                      style={{ width: "1em", height: "1em" }}
                    />{" "}
                    {tagMap[tag] || tag}
                  </span>
                </Link>
              ))}
            </div>
          </div>
        )}
      </main>
    </div>
  );
}

// Âä®ÊÄÅÁîüÊàêË∑ØÂæÑ
export const getStaticPaths: GetStaticPaths = async () => {
  const posts = getAllPosts();
  const paths = posts.map((post) => ({
    params: { slug: post.slug },
  }));

  return { paths, fallback: false };
};

// Ëé∑ÂèñÊØèÊù°ÊñáÁ´†ÂÜÖÂÆπ
export const getStaticProps: GetStaticProps = async ({ params }) => {
  const slug = params?.slug as string;
  const post = getPostBySlug(slug);

  return { props: { post } };
};
